import * as React from 'react'
import { Button, Layout, Toast } from '../auto-ui'

const Demo: React.FC<any> = () => {
  const onClick = () => {
    Toast('', 10000000)
  }

  const onClose = () => {
    Toast.close()
  }

  return (
    <Layout>
      <Layout.Body>
        <Button onClick={onClick}></Button>
        <br />
        <Button onClick={onClose}></Button>
      </Layout.Body>
    </Layout>
  )
}

export default Demo
